import React from 'react';
import AttendanceButton from './AttendanceButton';
import FormField from './FormField';
import SleepButton from './SleepButton';

export default function PersonForm(props) {
  return (
    <div className="form-group person-form p-3 mt-1 mb-2">
      <AttendanceButton
        person={props.person}
        handleChange={props.handleChange}
        show={true}
        swedish={props.swedish}
      />
      <FormField
        id={props.person.id}
        type="text"
        name="name"
        placeholder={props.swedish ? 'Förnamn...' : 'First Name...'}
        value={props.person.name}
        handleChange={props.handleChange}
        show={true}
        required={true}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="surname"
        placeholder={props.swedish ? 'Efternamn...' : 'Surname...'}
        value={props.person.surname}
        handleChange={props.handleChange}
        show={true}
        required={true}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="email"
        placeholder="Email..."
        value={props.person.email}
        handleChange={props.handleChange}
        show={props.person.isAttending && props.person.id === 1}
        required={props.person.isAttending && props.person.id === 1}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="diet"
        placeholder={
          props.swedish ? 'Specialkost...' : 'Dietary restrictions...'
        }
        value={props.person.diet}
        handleChange={props.handleChange}
        show={props.person.isAttending}
        required={false}
      />
      <FormField
        id={props.person.id}
        type="text"
        name="comments"
        placeholder={
          props.swedish ? 'Övrigt meddelande...' : 'Additional message...'
        }
        value={props.person.comments}
        handleChange={props.handleChange}
        show={props.person.id === 1}
        required={false}
      />

      <SleepButton
        person={props.person}
        handleChange={props.handleChange}
        show={props.person.isAttending}
        swedish={props.swedish}
      />
    </div>
  );
}
