import React from 'react';

export default function AttendanceButton(props) {
  return (
    props.show && (
      <div className="form-row justify-content-md-center mb-1">
        <div className="btn-group btn-group-toggle btn-block btn-sm">
          <label
            className={
              'btn btn-outline-success ' +
              (props.person.isAttending ? ' active' : '')
            }
          >
            <input
              type="radio"
              id={'attendingButton' + props.person.id}
              checked={props.person.isAttending}
              onChange={e => {
                props.handleChange(props.person.id, 'isAttending', true);
                e.preventDefault();
              }}
            />
            {props.swedish ? 'Jag kommer' : 'Attending'}
          </label>
          <label
            className={
              'btn btn-outline-danger ' +
              (!props.person.isAttending ? 'active ' : '')
            }
          >
            <input
              type="radio"
              id={'notAttendingButton' + props.person.id}
              checked={!props.person.isAttending}
              onChange={e => {
                props.handleChange(props.person.id, 'isAttending', false);
                e.preventDefault();
              }}
            />
            {props.swedish ? 'Tyvärr inte!' : 'Not attending'}
          </label>
        </div>
      </div>
    )
  );
}
