import React, { Component } from 'react';

import SubmitButton from './SubmitButton';
import PersonForm from './PersonForm';
import AdditionalPersonsForm from './AdditionalPersonsForm';
import Image from './Image';
import hearts from '../images/hearts.png';
import surprise from '../images/surprise.png';
import Spinner from './Spinner';
const defaultState = {
  submitted: 'none',
  persons: [
    {
      isAttending: true,
      id: 1,
      name: '',
      surname: '',
      diet: '',
      sleepFri: false,
      sleepSat: false,
      email: '',
      comments: ''
    }
  ]
};

export default class RSVPForm extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState;
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.addPerson = this.addPerson.bind(this);
    this.removePerson = this.removePerson.bind(this);
  }

  async handleSubmit(event) {
    event.preventDefault();
    this.setState({
      ...this.state,
      submitted: 'pending'
    });

    const scriptURL =
      'https://script.google.com/macros/s/AKfycbznWL2A-dRgzL-HGPDhYKjoA7UpAVCI6JHaiHRxNYIwE4UcgIg/exec';

    for (let i = 0; i < this.state.persons.length; i++) {
      let data = new FormData();
      for (let key in this.state.persons[i]) {
        data.append(key, this.state.persons[i][key]);
      }

      data.append('submittingPerson', this.state.persons[0].name);

      try {
        let response = await fetch(scriptURL, { method: 'POST', body: data });
        let b = await response.json();
        console.log(b);
      } catch (error) {
        this.setState({
          ...this.state,
          submitted: 'error'
        });
        return;
      }
    }

    this.setState(defaultState);

    this.setState({
      ...this.state,
      submitted: 'success'
    });
  }

  handleChange(id, name, value) {
    this.setState({
      ...this.state,
      persons: this.state.persons.map(person => {
        if (id === person.id) {
          return {
            ...person,
            [name]: value
          };
        } else {
          return person;
        }
      })
    });
  }

  addPerson() {
    this.setState({
      ...this.state,
      persons: [
        ...this.state.persons,
        {
          ...defaultState.persons[0],
          id: this.state.persons.length + 1
        }
      ]
    });
  }

  removePerson() {
    if (this.state.persons.length > 1) {
      this.setState({
        ...this.state,
        persons: this.state.persons.filter(
          person => person.id !== this.state.persons.length
        )
      });
    } else {
      this.setState(defaultState);
    }
  }

  render() {
    let form = (
      <div>
        <h3 className="text-center mb-4">
          {this.props.swedish ? ' Anmäl dig!' : 'RSVP'}
        </h3>
        <form onSubmit={this.handleSubmit}>
          <div className="row justify-content-md-center">
            <div className="col-md-6">
              {this.state.persons.map(person => (
                <PersonForm
                  key={person.id}
                  handleChange={this.handleChange}
                  person={person}
                  swedish={this.props.swedish}
                />
              ))}
              <AdditionalPersonsForm
                addPerson={this.addPerson}
                removePerson={this.removePerson}
                swedish={this.props.swedish}
              />
              <SubmitButton swedish={this.props.swedish} />
            </div>
          </div>
        </form>
      </div>
    );

    let success = (
      <div>
        <h3 className="text-center mb-4">
          {this.props.swedish ? 'Tack så mycket!' : 'Thank you very much! '}
        </h3>
        <div className="row justify-content-md-center">
          <div className="col-md-6">
            <Image img={hearts} />
          </div>
        </div>
      </div>
    );
    let error = (
      <div>
        <h3 className="text-center mb-4">
          {this.props.swedish
            ? 'Woops, något gick fel!'
            : 'Woops, something went wrong!'}
        </h3>
        <h5>
          {this.props.swedish
            ? 'Prova lite igen lite senare eller skicka ett email till antonstenaxel@gmail.com'
            : 'Try again a little later or send an email to antonstenaxel@gmail.com'}
        </h5>
        <div className="row justify-content-md-center">
          <div className="col-md-6">
            <Image img={surprise} />
          </div>
        </div>
      </div>
    );

    let content;

    switch (this.state.submitted) {
      case 'pending':
        content = <Spinner />;
        break;
      case 'error':
        content = error;
        break;
      case 'success':
        content = success;
        break;
      default:
        content = form;
        break;
    }

    return (
      <div>
        <section id={this.props.section} className="sec">
          {content}
        </section>
      </div>
    );
  }
}
