import React from 'react';

export default function SleepButton(props) {
  return (
    props.show && (
      <div>
        <div className="form-row">
          <div className="col text-center ">
            <label className=" align-bottom mb-0 mt-3">
              {props.swedish
                ? 'Klicka i de nätter du vill sova över'
                : 'Check the nights you want to book a bed'}
            </label>
          </div>
        </div>
        <div className="form-row">
          <div
            className="btn-group btn-group-toggle btn-block btn-sm"
            data-toggle="buttons"
          >
            <label
              className={
                'btn btn-outline-secondary ' +
                (props.person.sleepFri ? ' active' : ' disabled')
              }
            >
              <input
                type="checkbox"
                name="sleepButton"
                id={'sleepFri' + props.person.id}
                value="sleepFri"
                autoComplete="off"
                checked={props.person.sleepFri}
                onChange={e => {
                  props.handleChange(
                    props.person.id,
                    'sleepFri',
                    !props.person.sleepFri
                  );
                }}
              />
              {props.swedish ? 'Fre' : 'Fri'}
            </label>
            <label
              className={
                'btn btn-outline-secondary ' +
                (props.person.sleepSat ? 'active ' : ' disabled')
              }
            >
              <input
                type="checkbox"
                name="sleepButton"
                id={'sleepSat' + props.person.id}
                value="sleepSat"
                autoComplete="off"
                checked={props.person.sleepSat}
                onChange={e => {
                  props.handleChange(
                    props.person.id,
                    'sleepSat',
                    !props.person.sleepSat
                  );
                }}
              />
              {props.swedish ? 'Lör' : 'Sat'}
            </label>
          </div>
        </div>
      </div>
    )
  );
}
