import React from 'react';
import swe from '../images/sweden.png';
import uk from '../images/united-kingdom.png';

export default function LanguageButton(props) {
  return (
    <div>
      <button
        className="language-btn"
        type="button"
        onClick={props.changeLanguage}
      >
        <img className="flag-icon" src={props.swedish ? uk : swe} alt="" />
      </button>
    </div>
  );
}
