import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import './App.css';
import { text } from './text.js';
import Card from './components/Card';
import RSVPForm from './components/RSVPForm';
import LanguageButton from './components/LanguageButton';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      language: 'swe'
    };
    this.changeLanguage = this.changeLanguage.bind(this);
  }

  changeLanguage() {
    this.setState({
      language: this.state.language === 'swe' ? 'eng' : 'swe'
    });
  }
  render() {
    return (
      <div className="main-feed ">
        {text.map(section => (
          <Card
            key={section.section}
            section={section.section}
            title={section.title[this.state.language]}
            text={section.body[this.state.language]}
            img={section.img}
            right={section.right}
          >
            {section.section === 'start' && (
              <LanguageButton
                changeLanguage={this.changeLanguage}
                swedish={this.state.language === 'swe'}
              />
            )}
          </Card>
        ))}

        <RSVPForm section="rsvp" swedish={this.state.language === 'swe'} />
      </div>
    );
  }
}

export default App;
