import React from 'react';
export default function Card(props) {
  return (
    <div>
      <section id={props.section} className="sec p-4">
        <div className="row ">
          <div
            className={
              'col-md-8 ' + (props.right ? 'order-md-2' : 'order-md-1')
            }
          >
            <h3 className="sec-title">{props.title}</h3>
            <p
              className="sec-text"
              dangerouslySetInnerHTML={{ __html: props.text }}
            />
          </div>
          <div
            className={
              'col-md-4 align-self-center ' +
              (props.right ? 'order-md-1' : 'order-md-2')
            }
          >
            {props.img}
          </div>
        </div>
      </section>
      {props.children}
    </div>
  );
}
